/* --- Pricing Cards --- */
.pricing-card {
  position: relative;
  border: 0;
  border-top: 0.25rem solid #dee2e6;
  text-align: center;
  transition: all 0.2s;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}
.pricing-card:hover, .pricing-card.active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  opacity: 1;
}
.pricing-card-icon {
  position: absolute;
  font-size: 1.462rem;
  color: #fff;
  top: 0.625rem;
  right: 0.875rem;
}
.pricing-card-title {
  font-size: 1.308rem;
  font-weight: 300;
}
.pricing-card-price {
  padding: 0.385rem 0 1.154rem;
  font-size: 2.539rem;
}
.pricing-card-price .currency {
  vertical-align: text-top;
  font-size: 1rem;
  padding-right: 0.385rem;
}
.pricing-card-price .frequency {
  font-size: 1.308rem;
  font-weight: 300;
  vertical-align: text-bottom;
}
.pricing-card-features {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pricing-card-features li {
  font-size: 1rem;
  padding: 0.25rem 0;
  line-height: 1.7;
}
.pricing-card .card-header {
  padding-top: 1.538rem;
  padding-bottom: 1.538rem;
  margin-bottom: 0;
}
.pricing-card .card-body {
  padding-top: 1.538rem;
  padding-bottom: 1.538rem;
}
.pricing-card .card-footer {
  padding-top: 1.538rem;
  padding-bottom: 1.538rem;
}
.pricing-card .btn {
  min-width: 8rem;
}

.pricing-card-features-nosupport {
  opacity: 0.4;
  text-decoration: line-through;
}

.pricing-card-ribbon {
  position: absolute;
  top: 0;
  right: 0;
  height: 10rem;
  width: 10rem;
  overflow: hidden;
  cursor: default;
  user-select: none;
}
.pricing-card-ribbon span {
  height: 2.25rem;
  width: 15rem;
  line-height: 2.25rem;
  font-size: 0.8462rem;
  text-align: center;
  font-weight: bold;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  color: #fff;
  background-color: #dee2e6;
  transform: rotate(45deg);
  overflow: hidden;
  white-space: nowrap;
  position: absolute;
  top: 1.7142857143rem;
  right: -4.5rem;
}

.pricing-card-promo:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 4.5rem 4.5rem 0;
  border-color: transparent;
  border-right-color: #dee2e6;
}

.border-primary.pricing-card-promo:before {
  border-right-color: #4285f4;
}

.border-secondary.pricing-card-promo:before {
  border-right-color: #878787;
}

.border-success.pricing-card-promo:before {
  border-right-color: #34a853;
}

.border-info.pricing-card-promo:before {
  border-right-color: #6ba4ff;
}

.border-warning.pricing-card-promo:before {
  border-right-color: #ff9f00;
}

.border-danger.pricing-card-promo:before {
  border-right-color: #ea4335;
}

.border-light.pricing-card-promo:before {
  border-right-color: #f8f9fa;
}

.border-dark.pricing-card-promo:before {
  border-right-color: #343a40;
}

.border-konkon.pricing-card-promo:before {
  border-right-color: #fe669e;
}

